import React from 'react';
import PropTypes from 'prop-types';

import './MainLayout.css';

const Main = ({ children }) => (
  <>

    {/* header */}
    <main className="bg-beige h-screen ">{children}</main>
    {/* footer */}

  </>
);

Main.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Main;
